<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
          @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
          @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">
                    <el-col :span="5">
                        <span class="techo-list__header-col-txt-4">机构名称：</span>
                        <div class="techo-list__header-col-left-4">
                            <el-select placeholder="请选择综治机构" size="small" clearable filterable v-model="queryForm.orgId">
                                <el-option v-for="(item, index) in orgListOption" :label="item.name" :value="item.id" :key="index"></el-option>
                            </el-select>
                        </div>
                    </el-col>
                    <el-col :span="5">
                        <span class="techo-list__header-col-txt-4">姓名：</span>
                        <div class="techo-list__header-col-left-4">
                        <el-input placeholder="请输入姓名" :maxlength="10" size="small" class="techo-list__header-select-full"
                            clearable v-model="queryForm.name"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :title="title"></techo-details>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/zz_service.js";
import vAddDialog from "./zz-team-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "综治队伍",
      queryForm: {
        orgId: "",
        name: "",
        districtCode: ""
      },
      disCodeOption: [],
      orgListOption: [],
      header: {
        姓名: "name",
        性别: "dicMap.sex",

        //民族: "dicMap.nation",
        //政治面貌: "dicMap.politicsStatus",
        //身份证号: "idcardNo",
        //出生日期: "birth",
       级别: "dicMap.grade",
        职务: "dicMap.duty",
        //专业特长: "dicMap.specialty",
        //学历: "dicMap.education",

        // 民族: "dicMap.nation",
        // 政治面貌: "dicMap.politicsStatus",
        // 身份证号: "idcardNo",
        // 出生日期: "birth",
        级别: "dicMap.grade",
        职务: "dicMap.duty",
        // 专业特长: "dicMap.specialty",
        // 学历: "dicMap.education",
        手机号码: "phoneNumber",
        //固定电话: "telephoneNumber"
      },
      data: [],
      recordCount: 0,
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "zzTeam"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
    /** 根据区域编码查询综治机构 */
    service.requestOrgListByCode(this.queryForm.districtCode, data => {
      this.orgListOption = data.list;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    },
    "queryForm.districtCode": {
      handler(code) {
        service.requestOrgListByCode(code, data => {
          this.orgListOption = data.list;
        });
      }
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加综治队伍 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
      this.$refs["vAddDialog"].orgListOption = this.orgListOption;
    },
    /** 删除综治队伍 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteTeam(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "综治队伍删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "综治队伍删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      let data = _.cloneDeep(row);
      data.specialty = (data.specialty && data.specialty.split(",")) || [];
      this.$refs["vAddDialog"].dialogForm = data;
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        orgId: "",
        name: "",
        districtCode: constants.REGION.CODE
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageTeamList(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageTeamList(pageInfo, data => {
            this.recordCount = data.totalCount;
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          this.data = data.list;
        }
      });
    }
  }
};
</script>
