<template>
    <el-dialog class="add-team-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="100px">
            <el-row :gutter="15">
              <el-col :span="8">
                <el-form-item label="机构名称" prop="organizationId">
                    <el-select placeholder="请选择机构" clearable filterable v-model="dialogForm.organizationId" @change="handleChangeCode"> 
                        <el-option v-for="(item, index) in orgListOption" :label="item.name" :value="item.id" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="民族" prop="nation">
                    <el-select placeholder="请选择民族" clearable v-model="dialogForm.nation">
                        <el-option v-for="(item, index) in dic['民族']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>  
                <el-form-item label="出生日期" prop="birth">
                    <el-date-picker v-model="dialogForm.birth" type="date" placeholder="请选择日期"></el-date-picker>
                </el-form-item>  
                <el-form-item label="手机号码" prop="phoneNumber">
                    <el-input v-model="dialogForm.phoneNumber" placeholder="请填写手机号码"></el-input>
                </el-form-item>  
              </el-col>
              <el-col :span="8">
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="dialogForm.name" placeholder="请填写姓名"></el-input>
                </el-form-item>  
                <el-form-item label="政治面貌" prop="politicsStatus">
                    <el-select placeholder="请选择政治面貌" clearable v-model="dialogForm.politicsStatus">
                        <el-option v-for="(item, index) in dic['政治面貌']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="级别" prop="grade">
                    <el-select placeholder="请选择人员级别" clearable v-model="dialogForm.grade">
                        <el-option v-for="(item, index) in dic['人员级别']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="身份证号" prop="idcardNo">
                    <el-input v-model="dialogForm.idcardNo" placeholder="请填写身份证号码"></el-input>
                </el-form-item> 
              </el-col>
              <el-col :span="8">
                <el-form-item label="性别" prop="sex">
                    <el-select placeholder="请选择性别" clearable v-model="dialogForm.sex">
                        <el-option v-for="(item, index) in dic['性别']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="学历" prop="education">
                    <el-select placeholder="请选择学历" clearable v-model="dialogForm.education">
                        <el-option v-for="(item, index) in dic['学历']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="职务" prop="duty">
                    <el-select placeholder="请选择人员职务" clearable v-model="dialogForm.duty">
                        <el-option v-for="(item, index) in dic['人员职务']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="固定电话" prop="telephoneNumber">
                    <el-input v-model="dialogForm.telephoneNumber" placeholder="请填写固定电话"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="15">
              <el-col :span="24">
                <el-form-item label="专业特长">
                    <el-select placeholder="请选择专业特长" clearable multiple v-model="dialogForm.specialty">
                        <el-option v-for="(item, index) in dic['专业特长']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      orgListOption: [],
      dic: {
        性别: [],
        民族: [],
        政治面貌: [],
        人员级别: [],
        人员职务: [],
        专业特长: [],
        学历: []
      },
      dialogForm: {
        specialty: [],
        districtCode: "",
        nation: "01",
        sex: "1",
        politicsStatus: "13"
      },
      teamRules: {
        organizationId: [
          { required: true, message: "请选择综治组织机构", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 5, message: "长度在 2 到 5 个字符", trigger: "blur" }
        ],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        nation: [{ message: "请选择民族", trigger: "change" }],
        politicsStatus: [{ message: "请选择政治面貌", trigger: "change" }],
        idcardNo: [util.validate.isIDCard()],
        birth: [{ message: "请选择出生日期", trigger: "change" }],
        grade: [{ required: true, message: "请选择级别", trigger: "change" }],
        duty: [{ required: true, message: "请选择职务", trigger: "change" }],
        education: [{ message: "请选择学历", trigger: "change" }],
        phoneNumber: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          util.validate.valiTellPhone()
        ],
        telephoneNumber: [util.validate.tellPhone()]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  methods: {
    handleChangeCode(id) {
      if (id) {
        let code = this.orgListOption.find(item => item.id == id).districtCode;
        // 根据选中项重新获取区域编码
        this.dialogForm.districtCode = code;
      }
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.birth = util.time.formatDate(param.birth, "YYYY-MM-DD");
          param.specialty = param.specialty.join(",");
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveTeam(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "综治队伍添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateTeam(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "综治队伍修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {
        specialty: [],
        districtCode: "",
        nation: "01",
        sex: "1",
        politicsStatus: "13"
      };
    }
  }
};
</script>
<style>
.add-team-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-team-dialog .el-select {
  width: 100%;
}
</style>